import {mergeConfig} from "@/utils/utils";
import {PayloadAction} from "@reduxjs/toolkit";
import {EditState} from "../state";
import {history} from "@/utils/history";
import dayjs from "dayjs";

interface ReturnConfigPayload {
  targetConfig: any
  pageData?: any
  releaseStatus?: any
  commonComponents?: any
}

/**
 * 
 * @param state
 * @param action
 */
export function returnConfig(state: EditState, action: PayloadAction<ReturnConfigPayload>) {
  const {targetConfig, pageData, releaseStatus, commonComponents} = action.payload
  // 
  if (!state.defaultConfig && !releaseStatus) {
    state.defaultConfig = JSON.parse(JSON.stringify(targetConfig))
  }
  // 
  if (
    !releaseStatus &&
    (JSON.stringify(targetConfig.userSelectComponents) !== JSON.stringify(state.pageConfig.userSelectComponents) ||
      JSON.stringify(targetConfig.page) !== JSON.stringify(state.pageConfig.page))
  ) {
    state.uiConfig = {
      ...state.uiConfig,
      releaseStatus: {
        'test': [0, 0, 0],
        'pre-release': [0, 0, 0],
        'master': [0, 0, 0]
      }
    }
  }
  //  title 
  targetConfig.page = targetConfig.page || {schema: {}, props: {}};
  // merge 
  state.pageConfig = {
    ...state.pageConfig,
    ...mergeConfig(state.defaultConfig, targetConfig)
  }
  // 
  const currentIndex = targetConfig.currentIndex || 0;
  //  currentIndex = -1  title
  // 
  if (currentIndex === -1) {
    state.editConfig = {
      ...state.editConfig,
      currentComponent: {
        currentComponentSchema: state.pageConfig.page,
        component: state.pageConfig.page,
        type: '__page',
      }
    }
  } else {
    // 
    let component = state.pageConfig.userSelectComponents[currentIndex];
    if (component) {
      let currentComponentSchema = state.pageConfig.components.filter((c) => c.name === component.name)?.[0];
      // 
      if (component.name === 'mumu-remote-component-loader') {
        if (state.pageConfig.remoteComponents.length) {
          currentComponentSchema = state.pageConfig.remoteComponents.filter((c) => `${c.name}` === `${component.config.name}`)?.[0] || {};
        }
      }
      //  form-render
      state.editConfig = {
        ...state.editConfig,
        currentComponent: {
          currentComponentSchema,
          component,
        }
      }
    }
  }

  // 
  //  
  if (history.actionType) {
    history.push({
      ...state.pageConfig,
      actionType: history.actionType,
      createTime: dayjs().format('YYYY-MM-DD hh:mm:ss')
    })
  }
  state.uiConfig = {
    ...state.uiConfig,
    pageData: pageData || state.uiConfig.pageData,  // 
    releaseStatus: releaseStatus || state.uiConfig.releaseStatus,  // 
    commonComponents: commonComponents || state.uiConfig.commonComponents,  // 
  }

  // 
  state.editConfig = {
    ...state.editConfig,
    currentIndex
  }
}